.social {
  height: 50vh;
}
.chat {
  height: 50vh;
}

.Contact {
  position: relative;
  left: 2rem;
}

.Toastify__toast-container--top-center {
  top: 35%;
  left: 50%;
  transform: translateX(-50%);
}


@media only screen and (max-width: 480px) {
  .con {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
    padding: 10px;
  }
  .box {
    background-color: black;
  }

  .social {
    position: relative;
    bottom: 7rem;
  }

  .chat {
    position: relative;
    bottom: 14rem;
  }
}

